<template>
  <div class="swiper-content">
    <swiper
      ref="mySwiper"
      :options="swiperOptions"
      v-if="banners && banners.length !== 0"
    >
      <swiper-slide v-for="img in banners" :key="img.targetId">
        <img :src="`${img.imageUrl}?param=400x400`" alt="" />
      </swiper-slide>

      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "myswiper",
  props: {
    banners: {
      type: Array,
      require: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      swiperOptions: {
        pagination: {
          el: ".swiper-pagination", //与slot="pagination"处 class 一致
          clickable: true, //轮播按钮支持点击
          loop: true,
        },
      },
    };
  },
};
</script>

<style lang="less" scoped>
.swiper-content {
  margin-top: 90px;
  padding: 20px;
  height: 350px;
  .swiper-container {
    height: 350px;
    width: 100%;
    border-radius: 20px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>