const getters={
fullScreen(state){
    return state.fullScreen;
},
playList(state){
    return state.playList;
},
sequencesList(state){
    return state.sequencesList;
},
currentIndex(state){
    return state.currentIndex;
},
mode(state){
    return state.mode ;
},
currentSong(state){
    return state.playList[state.currentIndex]
}
}
export default getters;
