import SongList from "@/components/SongList";
import TopNav from "@/components/TopNav.vue"; 
import BetterScroll from "@/components/BetterScroll.vue";


export default {
    components: {
        SongList,
        TopNav,
        BetterScroll
    },
    data() {
        return {
          playDetailData: [],
        };
      },
    
    computed: {
        imgUrl() {
            if (this.playDetailData.length > 0) {
                return this.playDetailData[0].al.picUrl;
            }
            return "";
        },
        name() {
            if (this.playDetailData.length > 0) {
                return this.playDetailData[0].al.name;
            }
            return "暂无数据";
        }
    },


  
};
