<template>
  <div>
    <m-header v-if="commentData">评论{{ commentData.total }}</m-header>
    <div class="select-comment">
      <div class="title">评论区</div>
      <div class="select-option">
        <span :class="{ active: 0 === curIndex }" @click="handleClick(0)"
          >推荐</span
        >
        <span :class="{ active: 1 === curIndex }" @click="handleClick(1)"
          >最热</span
        >
      </div>
    </div>
    <better-scroll
      ref="refScroll"
      :data="commentData"
      class="betterScroll-warpper"
    >
      <comment-info
        :commentData="commentData"
        @emitFloorData="hasShowFloor"
      ></comment-info>
    </better-scroll>

    <!-- floor -->

    <div class="floor-content" v-show="isFloor" @click="offFloor">
      <transition name="fade" :duration="500">
        <div
          class="floor-warp"
          v-show="isFloor"
          @click.stop="handleStop($event)"
        >
          <m-header :red="false" class="floor-header"
            >{{ answerCount }} 条回复</m-header
          >
          <better-scroll
            ref="refFloorScroll"
            :data="floorData"
            class="betterScroll-floor-warpper"
          >
            <comment-info
              :commentData="floorData"
              @emitFloorData="hasShowFloor($event)"
              class="comment-info"
            ></comment-info>
          </better-scroll>
        </div>
      </transition>
    </div>

    <!-- floor - end -->
  </div>
</template>

<script>
import { getMusicComment, getCurFloorComment } from "@/api/requestHome";
import CommentInfo from "./components/CommentInfo.vue";
import mHeader from "@/components/mHeader.vue";
import BetterScroll from "@/components/BetterScroll.vue";

export default {
  name: "music-comment",
  data() {
    return {
      commentData: [],
      recommendCommentData: [],
      hotCommentData: [],
      floorData: [],
      curIndex: 0,
      isFloor: false,
      isFloorWarp: false,
      offset: 6,
      answerCount: 0,
      params: {
        type: 0,
        time: null,
        parentCommentId: null,
        id: 0,
      },
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.refScroll.scroll.on("pullingUp", async () => {
        if (this.curIndex === 0) {
          //请求数据
          const data = await getMusicComment({
            offset: this.offset,
            id: this.$route.params.id,
            limit: 10,
          });
          this.offset = this.offset + 11;

          //修改数据截取位置
          //添加数据
          this.recommendCommentData.push(...data.data.comments);
          //   this.commentData.push(...data.data.comments.slice(10));
          return false;
        }
      });
    });
    // 楼层回复数据
    this.$nextTick(() => {
      this.$refs.refFloorScroll.scroll.on("pullingUp", async () => {
        this.params.id = this.$route.params.id;
        this.params.time = this.floorData[this.floorData.length - 1].time;
        const data = await getCurFloorComment(this.params);
        this.floorData.push(...data.data.data.comments);

        // /comment/floor?parentCommentId=1438569889&id=29764564&type=0
      });
    });
  },
  provide() {
    return {
      id: this.$route.params.id,
    };
  },
  components: {
    CommentInfo,
    mHeader,
    BetterScroll,
  },
  methods: {
    //获取当前歌曲的评论
    async getMusicComment() {
      const { data } = await getMusicComment({
        id: this.$route.params.id,
        limit: 6,
      });
      //初始化数据
      this.commentData = data.comments;
      this.recommendCommentData = data.comments;
      this.hotCommentData = data.hotComments;
    },
    //显示弹出层 和 子组件传递过来的数据
    hasShowFloor(floorData) {
      this.isFloor = true;
      this.isFloorWarp = true;
      this.$refs.refFloorScroll.scroll.scrollTo(0, 0, 1000);

      this.floorData = floorData.commentsData;
      this.answerCount = floorData.len;

      this.params.parentCommentId = floorData.parentCommentId;
    },
    //隐藏弹出层
    offFloor() {
      this.isFloor = false;
    },
    // 1推荐 和 热门 选择
    handleClick(curindex) {
      this.curIndex = curindex;
      if (curindex == 0) {
        this.commentData = this.recommendCommentData;
        return false;
      }
      this.commentData = this.hotCommentData;
    },
    handleStop(e) {
      e.preventDefault();
    },
  },
  created() {
    this.getMusicComment();
  },
};
</script>

<style lang="less" scoped>
.betterScroll-warpper {
  height: calc(100vh - 140px);
  overflow: hidden;
}
.betterScroll-floor-warpper {
  height: calc(80vh - 90px);
  overflow: hidden;
  position: relative;
  top: 90px;
}

// 过渡动画
.fade-enter-active {
  transition: all 0.5s ease;
}
.fade-leave-active {
  background: rebeccapurple;
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter, .fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(30px);
  opacity: 0;
}

// 过渡动画 -end
.floor-content {
  z-index: 99;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.4);
  .floor-warp {
    width: 100vw;
    height: 80vh;
    border-radius: 50px 50px 0 0;
    position: fixed;
    bottom: 0;
    z-index: 99;
    background: whitesmoke;
    overflow: scroll;
    .floor-header {
      position: absolute;
      color: black;
      background: whitesmoke;
    }
    .comment-info {
      margin-top: 130px;
    }
  }
}

.select-comment {
  display: flex;
  justify-content: space-between;
  margin-top: 120px;
  padding: 0 30px;
  height: 50px;
  box-sizing: border-box;
  .select-option {
    span {
      margin: 0 5px;
      color: #aca1a1;
      &.active {
        color: black;
      }
    }
  }
}
</style>
